export default {
	data() {
		return {
			providerList: [],
			
		}
	},
	onLoad() {
		this.getLoginAuth()
	},
	methods:{
		//获取当前登录渠道
		getLoginAuth(){
			    uni.getProvider({
			        service: 'oauth',
			        success: (result) => {
			            this.providerList = result.provider.map((value) => {
			                let providerName = '';
							let icon='';
			                switch (value) {
			                    case 'weixin':
			                        providerName = '微信登录'
									icon='weixin'
			                        break;
			                    case 'qq':
			                        providerName = 'QQ登录'
									icon='QQ'
			                        break;
			                    case 'sinaweibo':
			                        providerName = '新浪微博登录'
									icon='xinlangweibo'
			                        break;
			                    case 'alipay':
			                        providerName = '支付宝登录'
									icon=''
			                        break;
			                }
			                return {
			                    name: providerName,
								icon:icon,
			                    id: value
			                }
			            });
			
			        },
			        fail: (error) => {
			            console.log('获取登录通道失败', error);
			        }
			    });
		},
		//登录
		tologin(provider) {
		    uni.login({
		        provider: provider.id,



		        success: (res) => {
					console.log(json.stringify(res))
		            console.log('login success:', res);
		            // 更新保存在 store 中的登录状态
		            console.log('灯枯成功，保存到本地存储，修改当前用户登陆状态')
		        },
		        fail: (err) => {
		            console.log('login fail:', err);
		        }
		    });
		}
	}
}
